export function toHump(str) {
  return str.replace(/\-(\w)/g, function(all, letter) {
    return letter.toUpperCase();
  });
}

/**
 * @description é©¼å³°è½¬ä¸­åçº¿
 * @param {string} str è¢«è½¬æ¢çå­ç¬¦ä¸²
 * @returns
 */
export function toLine(str) {
  return str.replace(/([A-Z])/g, "-$1").toLowerCase();
}

/**
 * @description å¤æ­ä¸ä¸ªèç¹æ¯å¦ä¸ºVNode
 * @param {HTMLNode} node domèç¹
 * @returns boolean
 */
export function isVNode(node) {
  return (
    node !== null &&
    typeof node === "object" &&
    hasOwnProperty.call(node, "componentOptions")
  );
}

/**
 * @description ç±»åå¤æ­
 * @param {*} obj åé
 * @returns {string} ç±»å
 */
function typeOf(obj) {
  const toString = Object.prototype.toString;
  const map = {
    "[object Boolean]": "boolean",
    "[object Number]": "number",
    "[object String]": "string",
    "[object Function]": "function",
    "[object Array]": "array",
    "[object Date]": "date",
    "[object RegExp]": "regExp",
    "[object Undefined]": "undefined",
    "[object Null]": "null",
    "[object Object]": "object"
  };
  return map[toString.call(obj)];
}

/**
 * @description å¯¹è±¡æ·±æ·è´
 * @param {object} data è¦æ·è´çå¯¹è±¡
 * @returns {object} æ·è´åçå¯¹è±¡
 */
export function deepCopy(data) {
  const t = typeOf(data);
  let o;

  if (t === "array") {
    o = [];
  } else if (t === "object") {
    o = {};
  } else {
    return data;
  }

  if (t === "array") {
    for (let i = 0; i < data.length; i++) {
      o.push(deepCopy(data[i]));
    }
  } else if (t === "object") {
    for (let i in data) {
      o[i] = deepCopy(data[i]);
    }
  }
  return o;
}

/**
 * @description æ¥ææ ¼å¼å
 * @param {string} fmt æ¾ç¤ºæ ¼å¼
 * @param {Date} date æ¥æå¯¹è±¡
 * @returns
 */
export function dateFormat(fmt, date) {
  let ret;
  const opt = {
    "Y+": date.getFullYear().toString(), // å¹´
    "m+": (date.getMonth() + 1).toString(), // æ
    "d+": date.getDate().toString(), // æ¥
    "H+": date.getHours().toString(), // æ¶
    "M+": date.getMinutes().toString(), // å
    "S+": date.getSeconds().toString() // ç§
    // æå¶ä»æ ¼å¼åå­ç¬¦éæ±å¯ä»¥ç»§ç»­æ·»å ï¼å¿é¡»è½¬åæå­ç¬¦ä¸²
  };
  for (let k in opt) {
    ret = new RegExp("(" + k + ")").exec(fmt);
    if (ret) {
      fmt = fmt.replace(
        ret[1],
        ret[1].length == 1 ? opt[k] : opt[k].padStart(ret[1].length, "0")
      );
    }
  }
  return fmt;
}
