<template>
  <div
    class="my-input"
    :class="{ 'my-input-disabled': disabled, [`my-input-${size}-size`]: true }"
    ref="myInput"
  >
    <input
      class="my-input-input"
      :class="{
        'my-input-input-disabled': disabled,
        'my-input-input-icon': needPaddingRight,
        'my-input-input-icon-suffix': prefixIcon,
        [`my-input-input-${size}-size`]: true,
      }"
      :type="type"
      :value="currentValue"
      @input="handleInput"
      @focus="handleFocus"
      :readonly="readonly"
      :placeholder="placeholder"
      :disabled="disabled"
    />
    <!-- åç½®å¾æ åºå -->
    <span class="my-input-icon my-input-icon-prefix">
      <i
        class="iconfont"
        :class="{
          [`${prefixIcon}`]: prefixIcon,
        }"
      />
    </span>
    <!-- åç½®å¾æ åºå -->
    <span class="my-input-icon">
      <i
        class="iconfont icon-close"
        v-if="clearable && currentValue && !disabled"
        @click="handleClean"
      />
      <i
        v-else
        class="iconfont"
        :class="{
          [`${suffixIcon}`]: suffixIcon,
        }"
      />
    </span>
    <!-- è¾å¥å»ºè®®éé¡¹æ¡ -->
    <transition name="fade-bottom">
      <div
        class="my-input-suggestion"
        v-show="panelVisible"
        ref="mySuggestion"
        v-if="suggestion && this.options.length > 0"
      >
        <div
          class="my-input-suggestion-cell"
          @click="setSuggestion(item.value)"
          v-for="item in options"
          :key="item.value"
        >
          <slot :item="item">
            {{ item.value }}
          </slot>
        </div>
        <!-- å è½½ä¸­æç¤º -->
        <div class="my-input-suggestion-layer" v-show="loading">
          <i class="iconfont icon-loading my-input-suggestion-layer-loading" />
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
// å·¥å·å½æ°ï¼ç¨äºå¤æ­ä¼ å¥çå¼æ¯å¦ç¬¦åæ¡ä»¶
import { oneOf, debounce } from '../../utils/assist'

export default {
  name: 'MyInput',
  data() {
    return {
      currentValue: this.value, // å½åè¾å¥å¼
      panelVisible: false, // é¼ æ å¨hoveré¶æ®µ
      options: [], // è¾å¥å»ºè®®å¯éé¡¹
      loading: false, // æ¯å¦æ­£å¨å è½½ä¸­
    }
  },
  props: {
    value: {
      type: String,
      default: '',
    },
    // å ä½ç¬¦
    placeholder: {
      type: String,
      default: 'è¯·è¾å¥',
    },
    max: {
      type: Number,
    },
    min: {
      type: Number,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    // æ¯å¦ç¦ç¨
    disabled: {
      type: Boolean,
      default: false,
    },
    // æ¯å¦æ¾ç¤ºæ¸ç©ºæé®
    clearable: {
      type: Boolean,
      default: false,
    },
    // åç½®å¾æ åç§°
    prefixIcon: {
      type: String,
      default: '',
    },
    // åç½®å¾æ åç§°
    suffixIcon: {
      type: String,
      default: '',
    },
    type: {
      type: String,
      default: "text"
    },
    // å°ºå¯¸
    size: {
      validator(value) {
        return oneOf(value, ['large', 'medium', 'small', 'mini'])
      },
      type: String,
      default: 'medium',
    },
    // æ¯å¦å¼å¯è¾å¥å»ºè®®
    suggestion: {
      type: Boolean,
      default: false,
    },
    // è¾å¥å»ºè®®åè°å½æ°
    fetchSuggestions: {
      type: Function,
    },
  },
  watch: {
    value: {
      handler(newVal) {
        this.currentValue = newVal
      },
      immediate: true,
    },
  },
  computed: {
    // æ¯å¦éè¦è®¾ç½®è¾å¥æ¡çå³ä¾§åè¾¹è·
    needPaddingRight() {
      return this.clearable && this.suffixIcon
    },
  },
  mounted() {
    // ç»å®ç¹å»äºä»¶
    document.addEventListener('click', this.addCloseEvent)
    this.getSuggesitions()
  },
  beforeDestroy() {
    // éæ¾ç¹å»äºä»¶
    document.removeEventListener('click', this.addCloseEvent)
  },
  methods: {
    /**
     * @description è¾å¥äºä»¶
     */
    handleInput(_e) {
      const value = _e.target.value
      this.currentValue = value
      this.$emit('input', value)
      this.$emit('change', value)
      debounce(
        () => {
          this.getSuggesitions()
        },
        333,
        'fetch-suggestions'
      )
    },
    /**
     * @description æ¸ç©ºè¾å¥å¼
     */
    handleClean() {
      if (!this.disabled) {
        this.currentValue = ''
        this.$emit('input', this.currentValue)
        this.$emit('change', this.currentValue)
        this.getSuggesitions()
      }
    },
    /**
     * @description inputçhoveräºä»¶ï¼è¯¥å½æ°ä½ç¨æ¯æ§å¶è¾å¥æç¤ºçæ¾ç¤º
     */
    handleFocus() {
      this.panelVisible = true
    },
    /**
     * @description æ§å¶æç¤ºæ¡çæ¾ç¤º/éè
     */
    addCloseEvent(event) {
      // ç¹å»ç®æ è¥ä¸æ¯ç»ä»¶ååç´ æ¶ï¼å³é­éé¡¹å¼¹çª
      let target = event.path.find((d) => d === this.$refs.myInput)
      if (!target && this.panelVisible) {
        this.panelVisible = false
      }
    },
    /**
     * @description è·åè¾å¥æç¤ºæ°æ®
     */
    getSuggesitions() {
      if (this.fetchSuggestions) {
        this.loading = true
        this.fetchSuggestions(this.currentValue, (options) => {
          this.loading = false
          this.options = options
        })
      }
    },
    /**
     * @description è®¾ç½®æéä¸­çå¼
     */
    setSuggestion(str) {
      this.currentValue = str
      this.$emit('input', this.currentValue)
      this.$emit('change', this.currentValue)
      this.panelVisible = false
    },
  },
}
</script>
